<template>
    <div class="ad-index">
        <div v-for="r in itemList">
            <h3 class="menber" style="color: red" :title="r.tips"> {{r.title}} </h3>
            <a :href="r.link" class="now-in" :title="r.tips" target="_blank">{{r.button }}</a>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'Ad_Top',
        components: {},
        data() {
            return {
                itemList: [],
                tempList:[],
                random: 0
            }
        },
        created() {
            this.getData();
        },
        methods: {
            getData() {

                this.$http.get('static/data/home_text_ad.json').then(res => {
                        for (var j = 0; j < res.data.length; j++) {
                            if (res.data[j].status == 1) {
                                this.tempList.push(res.data[j])
                            }
                        }

                var i = Math.floor(Math.random() * this.tempList.length);
//                        console.log(this.tempList.length)
                this.itemList.push(this.tempList[i])

                    },
                    res => {
                        console.log("error");
                    }
                )
            }
        }

    }

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



