<template>
    <div class="page-home">
        <!--banner-->
        <div class="banner">
            <div class="container">
                <h2>游戏推荐</h2>
                <div class="banner-matter">
                    <Slider />
                </div>
            </div>
        </div>
        <!--banner end-->
        <!--content-->
        <div class="content" style="display: block;">
            <div class="container">
                <!--content-top-->
                <div class="content-top">
                    <div class="col-md-9 top-col">
                        <!--Left-->
                        <div class="col-top">
                            <div class="col-md-6 black" v-for="n in navLeftList">
                                <a :href="'static/article/'+n.path+'.html'" target="_blank" v-if="n.html">
                                    <img class="img-responsive" :src="'static/images/index/'+n.path+'.jpg'" alt="">
                                    <div class="col">
                                        <h4>{{n.title}}</h4>
                                    </div>
                                </a>
                                <router-link :to="'/game/'+n.path" :title="n.title" target="_blank" v-if="!n.html">
                                    <img class="img-responsive" :src="'static/images/index/'+n.path+'.jpg'" alt="">
                                    <div class="col">
                                        <h4>{{n.title}}</h4>
                                    </div>
                                </router-link>
                            </div>

                            <div class="clearfix"></div>
                        </div>
                        <!--Text AD-->
                        <div class="col-top-bottom">
                            <div class="ad-block">
                                <AdIndex />
                            </div>

                            <div class="clearfix"></div>
                        </div>
                    </div>
                    <!--Right-->
                    <div class="col-md-3 per">
                        <a :href="'static/article/'+r.path+'.html'" v-for="r in navRightList" :title="r.title" target="_blank" v-if="r.html">
                            <img class="img-responsive" :src="'static/images/index/'+r.path+'.jpg'" alt="">
                            <div class="six">
                                <h4>{{r.title}}</h4>
                            </div>
                        </a>
                        <router-link :to="'/game/'+r.path" v-for="r in navRightList" :title="r.title" target="_blank" v-if="!r.html">
                            <img class="img-responsive" :src="'static/images/index/'+r.path+'.jpg'" alt="">
                            <div class="six">
                                <h4>{{r.title}}</h4>
                            </div>
                        </router-link>

                    </div>

                    <div class="clearfix"></div>
                </div>
            </div>
            <!--//content-top-->
            <!---->
            <div class="container">
                <div class="content-bottom">
                    <div class="col-md-6 bottom-content">
                        <h4>已汉化的游戏</h4>
                        <ul>
                            <li v-for="(game,index) in gameList" v-if="index<count">
                                <router-link :to="'/game/'+game.path" :title="game.title" target="_blank" v-if="!game.flash">
                                    <i class="glyphicon glyphicon-ok"></i>{{game.title}}（{{game.enTitle}}）
                                    <div class="date" v-if="game.update">
                                        最后更新：<span title="最后更新日期">{{game.update}}</span>
                                    </div>
                                </router-link>

                                <a :href="'static/flash/'+game.path+'.html'" :title="game.title" target="_blank" v-if="game.flash">
                                    <i class="glyphicon glyphicon-ok"></i>{{game.title}}（{{game.enTitle}}）
                                    <div class="date" v-if="game.update">
                                        最后更新：<span title="最后更新日期">{{game.update}}</span>
                                    </div>
                                </a>
                            </li>
                            <li>
                                <router-link to="/game">查看更多 ></router-link>
                            </li>
                        </ul>
                    </div>
                    <div class="col-md-6 bottom-content">
                        <h4>汉化中的游戏</h4>
                        <ul>
                            <li v-for="(game,i) in gameList2" v-if="i<count">
                                <router-link :to="'/game/'+game.path" :title="game.title" target="_blank" v-if="!game.flash">
                                    <i class="glyphicon glyphicon-pencil"></i>{{game.title}}（{{game.enTitle}}）
                                    <div class="date" v-if="game.update">
                                        最后更新：<span title="最后更新日期">{{game.update}}</span>
                                    </div>
                                </router-link>

                                <a :href="'static/flash/'+game.path+'.html'" :title="game.title" target="_blank" v-if="game.flash">
                                    <i class="glyphicon glyphicon-pencil"></i>{{game.title}}（{{game.enTitle}}）
                                    <div class="date" v-if="game.update">
                                        最后更新：<span title="最后更新日期">{{game.update}}</span>
                                    </div>
                                </a>
                            </li>
                            <li>
                                <router-link to="/game">查看更多 ></router-link>
                            </li>
                        </ul>
                    </div>

                    <div class="clearfix"></div>
                </div>
                <!--其它游戏-->
                <div class="content-bottom">
                    <div class="col-md-12 bottom-content">
                        <h4>其它游戏</h4>
                        <ul>
                            <li v-for="(game,index) in gameList3" v-if="index<count">
                                <router-link :to="'/game/'+game.path" :title="game.title" target="_blank" v-if="!game.flash">
                                    <i class="glyphicon glyphicon-search"></i>{{game.title}}（{{game.enTitle}}）
                                    <div class="date" v-if="game.update">
                                        最后更新：<span title="最后更新日期">{{game.update}}</span>
                                    </div>
                                </router-link>
                                <a :href="'static/flash/'+game.path+'.html'" :title="game.title" target="_blank" v-if="game.flash">
                                    <i class="glyphicon glyphicon-search"></i>{{game.title}}（{{game.enTitle}}）
                                    <div class="date" v-if="game.update">
                                        最后更新：<span title="最后更新日期">{{game.update}}</span>
                                    </div>
                                </a>
                            </li>
                            <li>
                                <router-link to="/game">查看更多 ></router-link>
                            </li>
                        </ul>
                    </div>

                    <div class="clearfix"></div>
                </div>
            </div>

        </div>
        <!--content-->
    </div>
</template>

<script>
import AdIndex from '@/components/Home/Ad_Top.vue'
import Slider from '@/components/Home/Slider.vue'
export default {
  name: 'Index',
  components: {
    AdIndex,
    Slider
  },
  data() {
    return {
      typeList: [],
      navLeftList: [],
      navRightList: [],
      gameList: [],
      gameList2: [],
      gameList3: [],
      curType: '已汉化',
      curType2: '汉化中',
      count: 20
    }
  },
  created() {
    this.getData()
    this.getNavData()
  },
  mounted() {

  },
  methods: {
    getData() {
      this.$http.get('static/data/games.json').then(game => {
        console.log(game)
        for (var i = 0; i < game.data.length; i++) {
          if (game.data[i].status === 1) {
            game.data[i].type.forEach(item => {
              if (item === this.curType) {
                this.gameList.push(game.data[i])
              } else if (item === this.curType2) {
                this.gameList2.push(game.data[i])
              }
            })
          }
        }
        // 其它游戏
        for (var i = 0; i < game.data.length; i++) {
            if (game.data[i].status === 1) {
          if ((game.data[i].type.indexOf(this.curType) === -1) && (game.data[i].type.indexOf(this.curType2) === -1)) {
            this.gameList3.push(game.data[i])
          }
        }
        }
        //                console.log(this.gameList3.length)
      })
    },
    getNavData() {
      this.$http.get('static/data/home_nav_ad.json').then(game => {
        // 左侧数据
        for (var i = 0; i < game.data[0].left.length; i++) {
          if (game.data[0].left[i].status === 1) {
            this.navLeftList.push(game.data[0].left[i])
          }
        }
        // 右侧数据
        for (var j = 0; j < game.data[1].right.length; j++) {
          if (game.data[1].right[j].status === 1) {
            this.navRightList.push(game.data[1].right[j])
          }
        }
      })
    }
  }
}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



